export default {
	name: "tuiActionsheet",
	props: {
		//点击遮罩 是否可关闭
		maskClosable: {
			type: Boolean,
			default: true
		},
		//显示操作菜单
		show: {
			type: Boolean,
			default: false
		},
		//菜单按钮数组，自定义文本颜色，红色参考色：#e53a37
		itemList: {
			type: Array,
			default: function() {
				return [{
					text: "确定",
					color: "#1a1a1a"
				}]
			}
		},
		//提示文字
		tips: {
			type: String,
			default: ""
		},
		//提示文字颜色
		color: {
			type: String,
			default: "#9a9a9a"
		},
		//提示文字大小 rpx
		size: {
			type: Number,
			default: 26
		},
		//是否需要取消按钮
		isCancel: {
			type: Boolean,
			default: true
		}
	},
	methods: {
		handleClickMask() {
			if (!this.maskClosable) return;
			this.handleClickCancel();
		},
		handleClickItem(e) {
			if (!this.show) return;
			const dataset = e.currentTarget.dataset;
			this.$emit('click', {
				index: dataset.index
			});
		},
		handleClickCancel() {
			this.$emit('cancel');
		}
	}
}
